export default class TagGambit {
  pattern() {
    return 'tag:(.+)';
  }
  toFilter(matches, negate) {
    const key = (negate ? '-' : '') + 'tag';
    return {
      [key]: matches[1].split(',')
    };
  }
  filterKey() {
    return 'tag';
  }
  fromFilter(value, negate) {
    return "".concat(negate ? '-' : '', "tag:").concat(value);
  }
}
flarum.reg.add('flarum-tags', 'common/query/discussions/TagGambit', TagGambit);